import React from 'react';
import s from './Dialogs.module.css';
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../Common/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validators";


const Dialogs = (props) => {
    let state = props.dialogsPage;
    let dialogsElement = state.dialogs.map(dialog => <DialogItem key={dialog.id}
                                                                 name={dialog.name}
                                                                 id={dialog.id}/>
    );

    let messagesElements = state.messages.map((message) => <Message key={message.id}
                                                                    message={message.message}
                                                                    id={message.id}/>
    );
   // let newMessageBody = state.newMessageBody;


    let addNewMessage = (values) => {
        props.sendMessage(values.newMessageBody)
    };


    return (

        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                {dialogsElement}
            </div>
            <div className={s.messages}>
                <div>{messagesElements}</div>
            </div>
            <div className={s.formDialogs} >
                <AddMessageFormRedux onSubmit={addNewMessage}/>
            </div >


        </div>
    )
};
const maxLength50 = maxLengthCreator(50);
const addMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea}
                       validate={[required, maxLength50]  }
                       name='newMessageBody' placeholder='Enter your message'/>
            </div>
            <div className={s.buttonCenter} >
                <button>Send</button>

            </div>
        </form>
    )
};
let AddMessageFormRedux = reduxForm({form: 'dialogAddMessageForm'})(addMessageForm);
export default Dialogs